.recipe{
    border-radius: 10px;
    box-shadow: 0px 5px 20px black;
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background-color: white;
    align-items: center;
    min-width: 40%;
}

.ingredients{
    text-align: center;
list-style: none;
overflow-wrap: break-word;
margin: 5% 5%;
}


.image{
    border-radius: 50%;
    width: 100px;
    height: 100px;
}

